export const Player = (playerId) => {
    const id = playerId;
    let score;

    const toString = () => {
        return "id: " + id + ", score: " + score;
    };

    const getId = () => {
        return id;
    }

    const getScore = () => {
        return score;
    }

    const setScore = (playerScore) => {
        if (playerScore < 0) {
            throw new Error("Score cannot be negative");
        }
        score = playerScore;
    }

    return {
        toString,
        getId,
        getScore,
        setScore
    };
}